<template>
  <div>
    <!-- 面包屑导航区域 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>商品分类</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 卡片视图区域 -->
    <el-card>
      <!-- 添加分类按钮 独占一行 -->
      <el-row>
        <el-col>
          <el-button type="primary" @click="showAddCateDialog"
            >添加分类</el-button
          >
        </el-col>
      </el-row>

      <!-- 表格区域 -->
      <tree-table
        class="treeTable"
        :data="cateList"
        :columns="columns"
        :selection-type="false"
        :expand-type="false"
        :show-row-hover="false"
        border
        show-index
        index-text=""
      >
        <!-- 是否有效 自定义模板列区域 -->
        <template slot="isok" slot-scope="scope">
          <i
            class="el-icon-success"
            v-if="scope.row.cat_deleted === false"
            style="color: lightgreen;"
          ></i>
          <i class="el-icon-error" v-else style="color: red;"></i>
        </template>

        <!-- 排序 自定义模板列区域 -->
        <template slot="level" slot-scope="scope">
          <el-tag v-if="scope.row.cat_level === 0">一级</el-tag>
          <el-tag v-else-if="scope.row.cat_level === 1" type="success"
            >二级</el-tag
          >
          <el-tag v-else type="warning">三级</el-tag>
        </template>

        <!-- 操作 自定义模板列区域 -->
        <template slot="manage" slot-scope="scope">
          <el-button
            type="primary"
            icon="el-icon-edit"
            size="mini"
            @click="showEditCateDialog(scope.row.cat_id)"
            >编辑</el-button
          >
          <el-button type="danger" icon="el-icon-delete" size="mini"
          @click="deleteCate(scope.row.cat_id)"
            >删除</el-button
          >
        </template>
      </tree-table>

      <!-- 分页区域 -->

      <el-pagination
        @size-change="changeSize"
        @current-change="changeNum"
        :current-page="queryInfo.pagenum"
        :page-sizes="[5, 10, 20, 40]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>

    <!-- 添加分类对话框区域 -->
    <el-dialog
      title="添加分类"
      :visible.sync="addCateDialogVisible"
      width="50%"
      @close="addCateDialogClosed"
    >
      <el-form
        :model="addCateForm"
        label-width="90px"
        :rules="addCateRules"
        ref="addCateRef"
      >
        <el-form-item label="分类名称:" prop="cat_name">
          <el-input v-model="addCateForm.cat_name"></el-input>
        </el-form-item>
        <el-form-item label="父级分类:">
          <el-cascader
            v-model="fatherCateKeys"
            :options="fatherCateList"
            :props="{
              expandTrigger: 'hover',
              value: 'cat_id',
              label: 'cat_name',
              children: 'children',
              checkStrictly: true
            }"
            @change="handleFatherCateChange"
            clearable
          ></el-cascader>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addCateDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="addCate">确 定</el-button>
      </span>
    </el-dialog>

    <!-- 编辑分类的对话框 -->
    <el-dialog
      title="编辑分类"
      :visible.sync="editCateDialogVisible"
      width="50%"
    >
      <el-form
        :model="editCateForm"
        :rules="editCateRules"
        ref="editCateRef"
        label-width="100px"
      >
        <el-form-item label="分类名称:" prop="cat_name">
          <el-input v-model="editCateForm.cat_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editCateDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="editCate"
          >确 定</el-button
        >
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data () {
    return {
      // 商品分类列表
      cateList: [],
      //   获取商品分类的请求参数
      queryInfo: {
        type: 3,
        pagenum: 1,
        pagesize: 5
      },
      //   商品分类总数
      total: 0,
      //   分类表格列的定义
      columns: [
        {
          label: '分类名称',
          prop: 'cat_name'
        },
        {
          label: '是否有效',
          //   模板列的自定义
          type: 'template',
          template: 'isok'
        },
        {
          label: '排序',
          //   模板列的自定义
          type: 'template',
          template: 'level'
        },
        {
          label: '操作',
          //   模板列的自定义
          type: 'template',
          template: 'manage'
        }
      ],
      //  控制添加分类的对话框显示或隐藏
      addCateDialogVisible: false,
      // 添加分类的表单
      addCateForm: {
        // 要添加的分类名称
        cat_name: '',
        // 要添加的分类父id
        cat_pid: 0,
        // 要添加的分类层级
        cat_level: 0
      },
      // 添加分类表单验证规则
      addCateRules: {
        cat_name: [
          { required: true, message: '请输入分类名称', trigger: 'blur' }
        ]
      },
      // 父级分类列表
      fatherCateList: [],
      // 选中的父级分类的id数组
      fatherCateKeys: [],
      // 控制编辑分类对话框的显示和隐藏
      editCateDialogVisible: false,
      // 编辑分类的表单
      editCateForm: {
        cat_name: ''
      },
      // 编辑分类表单验证规则
      editCateRules: {
        cat_name: [
          { required: true, message: '请输入分类名称', trigger: 'blur' }
        ]
      },
      // 正在编辑的分类Id
      editCateId: 0
    }
  },
  created () {
    this.getCateList()
  },
  methods: {
    // 监听获取商品分类事件
    async getCateList () {
      const { data: res } = await this.$axios.get('categories', {
        params: this.queryInfo
      })
      if (res.meta.status !== 200) {
        return this.$message.error('获取商品分类失败')
      }
      this.total = res.data.total
      this.cateList = res.data.result
    },
    // 分页区域 页码变动事件
    changeNum (newPage) {
      this.queryInfo.pagenum = newPage
      this.getCateList()
    },

    changeSize (newSize) {
      this.queryInfo.pagesize = newSize
      this.getCateList()
    },
    //  控制添加分类的对话框显示或隐藏
    showAddCateDialog () {
      this.getFatherCateList()
      this.addCateDialogVisible = true
    },
    // 获取父级分类列表
    async getFatherCateList () {
      const { data: res } = await this.$axios.get('categories', {
        params: { type: 2 }
      })
      if (res.meta.status !== 200) {
        return this.$message.error('获取父级分类失败')
      }
      this.fatherCateList = res.data
      console.log(this.fatherCateList)
    },
    // el-cascader选中项发生变化触发的事件
    handleFatherCateChange () {
      // 有父级分类的情况
      if (this.fatherCateKeys.length > 0) {
        // 定义当前选中父级分类的id
        this.addCateForm.cat_pid = this.fatherCateKeys[
          this.fatherCateKeys.length - 1
        ]
        // 定义当前选中分类的等级
        this.addCateForm.cat_level = this.fatherCateKeys.length
      } else {
        // 没有父级分类的情况
        this.addCateForm.cat_pid = 0
        this.addCateForm.cat_level = 0
      }
    },
    // 确认添加分类
    addCate () {
      this.$refs.addCateRef.validate(async valid => {
        if (!valid) return
        const { data: res } = await this.$axios.post(
          'categories',
          this.addCateForm
        )
        if (res.meta.status !== 201) return this.$message.error('添加分类失败')
        this.$message.success('添加分类成功')
        this.getCateList()
        this.addCateDialogVisible = false
      })
    },
    // 添加分类对话框关闭事件 （需求清空表单）
    addCateDialogClosed () {
      this.$refs.addCateRef.resetFields()
      this.fatherCateKeys = []
      this.addCateForm.cat_level = 0
      this.addCateForm.cat_pid = 0
    },
    // 显示编辑分类对话框
    async showEditCateDialog (id) {
      this.editCateId = id
      const { data: res } = await this.$axios.get(`categories/${id}`)
      if (res.meta.status !== 200) return this.$message.error('获取编辑分类失败')
      this.editCateForm.cat_name = res.data.cat_name
      this.editCateDialogVisible = true
    },
    // 完成分类编辑
    editCate () {
      this.$refs.editCateRef.validate(async valid => {
        if (!valid) return
        const { data: res } = await this.$axios.put(`categories/${this.editCateId}`,
          this.editCateForm)
        if (res.meta.status !== 200) return this.$message.error('编辑分类失败')
        this.$message.success('编辑分类成功')
        this.getCateList()
        this.editCateDialogVisible = false
      })
    },
    // 删除分类
    async deleteCate (id) {
      const rel = await this.$confirm('此操作将永久删除该分类, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).catch(err => err)
      if (rel !== 'confirm') return this.$message.info('已取消删除')
      const { data: res } = await this.$axios.delete(`categories/${id}`)
      if (res.meta.status !== 200) return this.$message.error('删除分类失败')
      this.$message.success('删除分类成功')
      this.getCateList()
    }
  }
}
</script>

<style lang="less" scoped>
.treeTable {
  margin-top: 20px;
}

.el-cascader {
  width: 100%;
}
</style>
